
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import AlertPlugin from 'vux/src/plugins/alert/index.js'

import {informationShow, personalPic, upDatePersonalPic, postPersonalPic} from '@/api/personal'
import {appUpdateSce} from '@/api/home';
import {helpQuery} from '@/api/help';
import WX_CONFIGS from '@/utils/wx-config';
import {EXIF} from '@/utils/exif';//

export default {
name: 'personal',
components: {
Cell,
CellBox,
CellFormPreview,
Group,
Badge,
AlertPlugin,
EXIF,
},
data() {
return {
isLoadingData: true,
quickMenuList: [],
message: '',
isHelpOpen: false,
imgList: [],
isMenuOpen: false,
imgSrc: "",
headerImage: ",picValue:",
customDetailList: "",
}
},
methods: {
upload (e) {//
let files = e.target.files || e.dataTransfer.files;
if (!files.length) return;
this.picValue = files[0];
this.imgPreview(this.picValue);
this.postImg(this.picValue);


},
postImg (param) {//
let image = new FormData();
image.append('file', param);
this.$vux.loading.show({text: ''});
postPersonalPic(image).then(response => {
console.log("", response.data.rows[0].objectUrl);
if (response.data) {

upDatePersonalPic({"avatar": response.data.rows[0].objectUrl, "accountNumber": "ADMIN"}).then(response => {
console.log(response.data.success);//true
if (response.data.success) {
this.$vux.loading.hide();
this.$vux.toast.show({text: ''});
}


}).catch(error => {

});
} else {

}
}).catch(error => {

});
},
imgPreview (file) {//
let self = this;
let Orientation;//
EXIF.getData(file, function () {
Orientation = EXIF.getTag(this, 'Orientation');
});

if (!file || !window.FileReader) return;
if (/^image/.test(file.type)) {

let reader = new FileReader();

reader.readAsDataURL(file);

reader.onloadend = function () {
let result = this.result;
let img = new Image();
img.src = result;

if (this.result.length <= (100 * 1024)) {
self.headerImage = this.result;
} else {
img.onload = function () {
let data = self.compress(img, Orientation);
self.headerImage = data;
}
}
}
}
},
rotateImg (img, direction, canvas) {


const min_step = 0;
const max_step = 3;
if (img === null)return;

let height = img.height;
let width = img.width;
let step = 2;
if (step === null) {
step = min_step;
}
if (direction === 'right') {
step++;

step > max_step && (step = min_step);
} else {
step--;
step < min_step && (step = max_step);
}

let degree = step * 90 * Math.PI / 180;
let ctx = canvas.getContext('2d');
switch (step) {
case 0:
canvas.width = width;
canvas.height = height;
ctx.drawImage(img, 0, 0);
break;
case 1:
canvas.width = height;
canvas.height = width;
ctx.rotate(degree);
ctx.drawImage(img, 0, -height);
break;
case 2:
canvas.width = width;
canvas.height = height;
ctx.rotate(degree);
ctx.drawImage(img, -width, -height);
break;
case 3:
canvas.width = height;
canvas.height = width;
ctx.rotate(degree);
ctx.drawImage(img, -width, 0);
break;
}
},
compress(img, Orientation) {        let canvas = document.createElement("canvas");
let ctx = canvas.getContext('2d');

let tCanvas = document.createElement("canvas");
let tctx = tCanvas.getContext("2d");
let initSize = img.src.length;
let width = img.width;
let height = img.height;

let ratio;
if ((ratio = width * height / 4000000) > 1) {
console.log("400")
ratio = Math.sqrt(ratio);
width /= ratio;
height /= ratio;
} else {
ratio = 1;
}
canvas.width = width;
canvas.height = height;

ctx.fillStyle = "#fff";
ctx.fillRect(0, 0, canvas.width, canvas.height);

let count;
if ((count = width * height / 1000000) > 1) {
console.log("100W");
count = ~~(Math.sqrt(count) + 1); //

let nw = ~~(width / count);
let nh = ~~(height / count);
tCanvas.width = nw;
tCanvas.height = nh;
for (let i = 0; i < count; i++) {
for (let j = 0; j < count; j++) {
tctx.drawImage(img, i * nw * ratio, j * nh * ratio, nw * ratio, nh * ratio, 0, 0, nw, nh);
ctx.drawImage(tCanvas, i * nw, j * nh, nw, nh);
}
}
} else {
ctx.drawImage(img, 0, 0, width, height);
}

if (Orientation !== "" && Orientation !== 1) {
switch (Orientation) {
case 6://90
this.rotateImg(img, 'left', canvas);
break;
case 8://90
this.rotateImg(img, 'right', canvas);
break;
case 3://180
this.rotateImg(img, 'right', canvas);//
this.rotateImg(img, 'right', canvas);
break;
}
}

let ndata = canvas.toDataURL('image/jpeg', 0.1);
console.log('' + initSize);
console.log('' + ndata.length);
console.log('' + ~~(100 * (initSize - ndata.length) / initSize) + "%");
tCanvas.width = tCanvas.height = canvas.width = canvas.height = 0;
return ndata;
},

openMenu(){
this.isMenuOpen = true;
},
closeMenu(){
this.isMenuOpen = false;
},
openHelp(){
this.isHelpOpen = true;
},
closeHelp(){
this.isHelpOpen = false;
},
goTarget(item) {
window.localStorage.setItem('workflowStatus', "1");
window.localStorage.setItem('applyStatus', "1");
console.log('goTarget.menu:' + JSON.stringify(item));
if (item.menuName === "") {
window.location.href = item.menuResource;
} else {
let selectItem = item.menuResource.split("/#/")[1];
let url = selectItem.split("/##/")[0];
let param = selectItem.split("/##/")[1];
if (selectItem && param === undefined) {
this.$router.push('/' + url);
} else {
this.$router.push({
name: url,
query: {
"param": param,
}
});
}
}
},
},
mounted () {
informationShow({"page": "1", "pagesize": "10"}).then(response => {
if (response.data.success) {
this.personal = response.data.rows;
} else {
this.personal = [];
}
}).catch(error => {
console.log(error.response);
});

appUpdateSce({"appEquipment": "Android"}).then(response => {
if (response.data && response.data) {
this.quickMenuList = response.data.menuCateories;
this.isLoadingData = false;
} else {
this.isLoadingData = false;
}
}).catch(error => {
this.isLoadingData = false;
});


helpQuery({"page": "1", "pagesize": "10", "hrPageconfCode": "B001"}).then(response => {
if (response.data && response.data) {
this.message = response.data.rows[0].hrPageconfDocs;
} else {
}
}).catch(error => {
});

personalPic({}).then(response => {
if (response.data) {
this.customDetailList = response.data;//
this.headerImage = this.customDetailList.avatar;//
console.log(this.customDetailList);
this.isLoadingData = false;
} else {
this.isLoadingData = false;
}
}).catch(error => {
this.isLoadingData = false;
});
},
}



