import Vue from 'vue';
import WechatPlugin from 'vux/src/plugins/wechat/index.js'

import {fetch} from './fetch';
import axios from 'axios';

Vue.use(WechatPlugin);

console.log(WechatPlugin.$wechat);

/**
* @getCode function from the wxURL:
* eg. url: http://handhrhap.hand-china.com/dist/?code=zCGiYN30SxNDeyN7yLn2n3iPC8mDPi0ZigeHDWL3L7U&state=STATE
* urlcode
*
* @getToken function get TOKEN through getCode function
* codetoken
*
* @wxConfig function JS-SDKJS-SDKapi
* eg. chooseImage api (ps: )
*/

let WX_CONFIGS = {
getCode() {
let WxUrlObj = {};
let urlParamsArray = window.location.search.substring(1).split('&');

urlParamsArray.forEach((item, index, array) => {
let pos = item.indexOf('=');
if (pos == '-1') {
} else {
WxUrlObj[item.split('=')[0]] = item.split('=')[1];
}
});

return WxUrlObj.code;
},
getToken() {
let code = this.getCode();
console.log(code);
if (code === window.localStorage.code) {
let p = new Promise(function (resolve, reject) {
resolve("noToken");
});
return p;
} else {
let p = new Promise(function (resolve, reject) {

let username = '3705', password = 'admin', loginUrl;

window.localStorage.code = "location";
loginUrl = process.env.baseUrl + 'oauth/token?client_id=a765ff3f-8d33-49b4-a9c9-48e991544242&client_secret=ec2a9a5d-8b21-40fb-b20b-fa846b944e7b&grant_type=password&username=' + username + '&password=' + password;




let instance = axios.create({
timeout: 30000,
headers: {'Content-Type': 'application/x-www-form-urlencoded'}
});
instance.post(loginUrl).then(function (response) {
if (response && response.data && typeof response.data === 'object') {
resolve(response.data);
}
}, function (error) {

console.log(error);
reject();
});
});
return p;
}
},
wxConfig(WxUrl, apiList) {

fetch({
url: 'api/getJSSDKParams',
method: 'post',
data: {
url: WxUrl
}
}).then(function (res) {
/* alert("configres"+JSON.stringify(res));*/
const data = res.data.rows[0] || [];
WechatPlugin.$wechat.config({
debug: false, // ,apialertpclogpc
appId: data.appId || '', // corpid
timestamp: data.timestamp || '', // 
nonceStr: data.nonceStr || '', // 
signature: data.signature || '',// 
jsApiList: apiList || [] // JS
});
});
}
};

export default WX_CONFIGS;





