import {fetch, upLoadFetch} from '@/utils/fetch';

/**
 * 
 * @param {*} bodyParams
 */
export const informationShow = (bodyParams) => fetch({
  url: 'api/hremp/query',
  method: 'post',
  data: bodyParams
});

/**
 * 
 * @param {*} params
 */
export const personalPic = (params) => fetch({
  url: 'i/api/staff/customDetail',
  method: 'post',
  data: params
});

/**
 *   i/api/staff/updateCustomDetail
 * @param {*} params
 */
export const upDatePersonalPic = (params) => fetch({
  url: 'i/api/staff/updateCustomDetail',
  method: 'post',
  data: params
});

/**
 * 
 * @param {*} params
 */
export const postPersonalPic = (params) => upLoadFetch({
  url: 'api/iconUpload',
  method: 'post',
  data: params
});


/**
 * 
 * @param {*} bodyParams
 */
export const informationList = (bodyParams) => fetch({
  url: 'api/hrpersonalinformationclass/query',
  method: 'post',
  data: bodyParams
});





